@import 'font-awesome.less';
.body {
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 14px;
    background: #f9f9f9;
    color: #191919;
    position: relative;
}

.wrapper {
    max-width: 1900px;
    margin: 0 auto;
}

.menu,
.close {
    font-size: 32px;
    color: #191919;
    cursor: pointer;
    opacity: 0.7;
    &:hover {
        opacity: 1;
    }
}

.close {
    font-size: 24px;
    position: absolute;
    right: 10%;
}

.menu-block {
    position: fixed;
    z-index: 1;
    width: 100vw;
    &__item {
        margin: 25px;
    }
}

.nav-block {
    position: fixed;
    z-index: 3;
    height: 100vh;
    left: -261px;
    background: #f9f9f9;
    &__item {
        margin: 25px 0;
    }
    &_margin {
        margin-left: 25px;
        margin-right: 25px;
    }
    &__link {
        color: inherit;
        background: inherit;
    }
}

.nav-list {
    padding-left: 0;
    &__item {
        list-style: none;
        padding: 15px 25px;
        font-size: 1.3em;
        &:hover {
            background: rgba(0, 0, 0, 0.05);
        }
    }
    &__link {
        color: inherit;
        text-decoration: none;
    }
    &__text {
        margin-left: 20px;
    }
}

.content-block {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    flex-wrap: wrap;
    &_b-indent {
        align-self: flex-end;
        margin-bottom: 40px;
    }
    &_t-indent {
        align-self: center;
    }
    &__items {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    &__list {
        list-style: circle;
    }
    &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 25px;
        box-sizing: border-box;
    }
    &__text {
        font-size: 1.2em;
        line-height: 1.5;
        margin: 0;
        padding: 0 25px;
    }
}

.fb-size_100 {
    flex-basis: 100%;
}

.fb-size_50 {
    flex-basis: 50%;
}

.content-list {
    font-size: 1.2em;
    margin: 0;
    padding-left: 25px;
    &__item {
        margin: 10px 0;
        list-style: none;
        line-height: 1.5;
    }
    &__img {
        margin-right: 8px;
    }
}

.logo {
    width: 32px;
    opacity: 0.8;
    display: flex;
    align-items: center;
    margin: 0 5px 0 15px;
    &:hover {
        opacity: 1;
    }
}

.main-logo {
    text-align: center;
}

.head-stope {
    display: flex;
    align-items: center;
}

.logo-text {
    font-size: 2em;
    margin: 0 10px;
    font-weight: bold;
}

.home {
    opacity: 1;
}

.info {
    background: inherit;
}

.portfolio {
    background: #2E95B7;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 2;
    opacity: 0;
}

.main-title {
    margin-top: 55px;
    font-weight: bold;
    font-size: 2.1em;
    opacity: 0.8;
    text-align: center;
}

.sub-title {
    font-weight: 400;
    font-size: 1.9em;
    opacity: 0.8;
    text-align: center;
    margin-top: -10px;
}

.title-block {
    font-size: 1.8em;
    font-weight: bold;
    text-transform: uppercase;
    margin-top: 50px;
    opacity: 0.8;
}

.color-blue {
    color: #00b7f4;
}

.color-white {
    color: #f9f9f9;
}

.img {
    margin: 5px;
    width: 100%;
    height: auto;
}

.img-slider {
    width: 100%;
    height: auto;
}

.img-double-down {
    color: #00b7f4;
    margin-top: auto;
    cursor: pointer;
}

.section {
    width: 100%;
    padding-top: 25px;
}

.hidden {
    overflow: hidden;
}

.visible {
    display: block;
}

.position {
    position: relative;
}

.slider {
    width: 500px;
    @media screen and (max-width: 576px) {
        width: 400px;
    }
    @media screen and (max-width:380px) {
        width: 300px;
    }
    &__link {
        display: block;
        transition: opacity 0.5s;
        &:hover {
            opacity: 0.1;
        }
    }
    &__block {
        background: #191919;
        color: #f9f9f9;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 1.8em;
        font-weight: bold;
        text-transform: uppercase;
        letter-spacing: 0.1em;
    }
}

.f-block {
    position: relative;
    &__ratio {
        height: 0;
        &_16-9 {
            padding-top: 56.25%;
        }
        &_1-1 {
            padding-top: 100%;
        }
        &_81 {
            padding-top: 81%;
        }
    }
    &__content {
        border: none;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.owl-stage-outer {
    box-shadow: 0px 0px 15px 0 rgba(0, 0, 0, 0.5);
}